
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


.header > nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 5.5vw;
    background-color: #1d5276; 
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  
}



.header > nav > span > img {
 
    width: auto;
}


.header > nav > .ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1vw;
}


.header > nav > .ul > .nav {
    cursor: pointer;
    margin: 3px;
    padding: 8px;
    color: #fff; 
    font-weight: 500;
    text-decoration: none;
    transition: color 0.3s ease, background-color 0.3s ease, box-shadow 0.3s ease; 
}


.header > nav > .ul > .nav:hover {
    color: #1d5a83; 
    background-color: #fff;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2); 

}


.header > nav > span > img {
height: 65px;
}
